// Stic footer on bottom - on scroll too
.dx-stick-footer {
   position:fixed;
   left:0px;
   bottom:0px;
   height:80px;
   width:100%;
   z-index: 5000;
   border-top: 1px solid darkgray;
   padding-left: 40px;
   background-color: #f1f4f7;
}

.dx-stick-footer .dx-left {
    height:80px;
    background-color: #f1f4f7;
    border-right: 1px solid lightgray;
    padding-top: 10px;
}

.dx-stick-footer .dx-right {
    height:80px;
    background-color: #e4ebef;
    padding-top: 23px;
    padding-left: 36px
}

.dx-stick-footer .dx-left .dx-empl-title {
    padding-left: 10px;
    font-weight: bold;
}